<template>
    <div id="sign-up" class="sign-up">
        <div v-if="errors.length">
            <strong>Please correct the following error(s):</strong>
            <ul>
                <li v-for="(error, i) in errors" :key="i">{{ error }}</li>
            </ul>
        </div>
        <form autocomplete="false">
            <div class="form-group">
                <input class="form-control" type="text" v-model="comment.fullnames" placeholder="Fullnames">
            </div>
            <div class="form-group">
                <input class="form-control" type="email" v-model="comment.email" placeholder="Email">
            </div>
            <div class="form-group">
                <textarea class="form-control" v-model="comment.comment" placeholder="Comment"></textarea>
            </div>
        </form>
        <button class="btn btn-theme" v-on:click.prevent="createComment">{{ $t('addfeedback') }}</button>
    </div>
</template>
<script>
    const fb = require("@/firebase/Firebase");
    export default {
        data: function() {
            return {
                errors: [],
                comment:{
                    email: '',
                    fullnames: '',
                    comment: '',
                    timestamp: '',
                    published: 0
                }
            }
        },
        methods: {
            clearForm: function() {
                for (let field in this.comment) 
                {
                    if( field != 'published')
                        this.comment[field] = ''
                }
            },
            createDate: function () {
                let format = function(date) {
                    var hours = date.getHours();
                    var minutes = date.getMinutes();
                    var ampm = hours >= 12 ? 'pm' : 'am';
                    hours = hours % 12;
                    hours = hours ? hours : 12; // the hour '0' should be '12'
                    minutes = minutes < 10 ? '0'+minutes : minutes;
                    var strTime = hours + ':' + minutes + ' ' + ampm;
                    return date.getMonth()+1 + "/" + date.getDate() + "/" + date.getFullYear() + " " + strTime;
                }
                return format( new Date() );
            },
            createComment: function() {
                let _this = this;
                if ( this.checkForm() )
                {   
                    this.comment.timestamp = this.createDate() 
                    fb.db.collection('comments').add({ comment: this.comment })
                    .then(function(docRef) {
                        _this.clearForm();
                        _this.flashMessage.success({ message: 'Comment was submited. Pending Moderation!'});
                        console.log('Comment saved with ID: ', docRef.id)
                    })
                    .catch((error) => {
                        _this.flashMessage.error({ message: 'Error Saving Comment. Please try again!'});
                        console.log("Error saving comment: ", error);
                    });
                }
            },
            checkForm: function () {
                this.errors = [];

                if( !this.comment.fullnames)
                    this.errors.push('Names are required.');

                if (!this.comment.email || ! this.validEmail(this.comment.email))
                    this.errors.push('Valid email required.');

                if (!this.comment.comment || this.comment.comment.length < 4)
                    this.errors.push('Comment must have at least 4 characters.');

                return (! this.errors.length) ? true : false;
            },
            validEmail: function (email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            }
        }
    }
</script>
