</template>
<script>
    import createComments from "@/components/Comments.vue";
    const fb = require("@/firebase/Firebase");
    export default {
        components: {
            'app-create-comments': createComments
        },
        data () {
            return {
                loading: true,
                publishedComments: {}
            }
        },
        created: function () {
            fb.db.collection('comments').get().then((row) => {
                this.loading = false;
                row.forEach((doc) => {
                    this.publishedComments[doc.id] = doc.data().comment;
                });
            });
        },
        methods: {
        }
    } 
</script>